.header-control {
    /* background-image: url('../../assets/DickBackground.png'); */
    background-color: #FFE1C9;
    /* this can be changed to header-back or dickbackground */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 120px;
    background-repeat: repeat;
    /* background-size: cover; */
    background-position: 50% 100%;
}

.header-control .navbar {
    width: 80%;
    background-color: #FF9859;
    margin-top: 0px;
    padding-top: 0px;
    border-radius: 20px;
    border: solid black 10px;
    /* outline: 5px solid black; */
    /* box-shadow: 0 0 0 3px rgb(0, 0, 0); */
}

.navbar-nav {
    display: flex;
    align-items: center;
}
.navbar-nav .nav-link{
    color: rgb(0, 0, 0)!important;
    font-size: 30px;
    font-weight: 700;
}

.navbar-nav .nav-link:hover{
    color: #ffffff!important;
}

.header-content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

@font-face {
    font-family: newfont;
    src: url(/src/newfont.ttf);
}

.header-content h1 {
    font-size: 51px;
    font-family: newfont;
    color: black;
    /* font-style: italic; */
    margin-bottom: 30px;
    margin-top: 30px;
    font-weight: 900;
    text-align: center;
    background-color: #6D3612;
    padding: 25px;
    border-radius: 20px;
    border: solid 5px black;
}

.header-nav {
    position: absolute;
    top: 20px;
    max-width: 1200px;
    width: 1200px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.nav-item {
    margin-right: 20px;
    color: white;
    font-size: 13px;
    font-weight: 700;
    cursor: pointer;
}
.nav-item :hover {
    color: blue;
}

@media (min-width: 767px) {
    .header-control {
        background-image: url('../../assets/DickBackground.png');
        /* this can be changed to header-back or dickbackground */
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        position: relative;
        padding-top: 20px;
        padding-bottom: 20px;
        height: 800px;
        background-repeat: repeat-x;
        /* background-size: cover; */
        background-position: 100% 40%;
    }
}